import React from "react";
import Modal from "react-modal";
import "../App.css";
import { useState } from "react";

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    background: "black",
    transform: "translate(-50%, -50%)",
    font: "Times",
  },
};

export function Modalbuy() {
  const [modalIsOpen, setIsOpen] = useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
  }

  return (
    <div>
      <button class="btn-dark btn-lg mt-3" onClick={openModal}>
        Buy Ticket(s) Now!
      </button>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        style={customStyles}
      >
        <h2 class="text-light">Your tickets will be sent to you soon!</h2>
        <center>
          <button onClick={closeModal}> X </button>{" "}
        </center>
      </Modal>
    </div>
  );
}
